<template>
    <div class="login_container">
      <div class="login_box">
        <div class="avatar_box">
            <img src="../assets/logo.png" alt=""/>
        </div>
         <!-- 表单区域 -->
         <el-form :model="loginForm"  ref="loginFormRef" :rules="loginFormRules" label-width="0px"  class="login_form">
          <el-form-item  prop="name"><!-- 用户名 -->
            <el-input v-model="loginForm.name"  prefix-icon="el-icon-user-solid"></el-input>
          </el-form-item>
          <el-form-item  prop="password"><!-- 密码 -->
            <el-input  v-model="loginForm.password"  prefix-icon="el-icon-lock" type="password"></el-input>
          </el-form-item>
          <el-form-item  class="btns"><!-- 按钮区域 -->
            <el-button type="primary"  @click="login">登录</el-button>
            <el-button type="info"  @click="resetLoginForm">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    
  </template>
   
  <script>
  export default {
    data(){
      return{
        loginForm:{
          name: 'LiHua',
          password: '123',
          id: 0,
          sex: 1,
          age: 0,
          classes: 0,
          score: 0
        },
        //表单验证规则对象
        loginFormRules:{
          //验证用户名是否合法
          name:[
            { required: true, message: '请输入登录名', trigger: 'blur' },
            { min: 3, max: 10, message: '长度在 3 到 10 个字符', trigger: 'blur' }
          ],
          //验证密码是否合法
          password:[
            { required: true, message: '请输入登密码', trigger: 'blur' },
            { min: 0, max: 15, message: '长度在 0 到 15 个字符', trigger: 'blur' }
          ]
        }
      }
    },
    methods:{
    //点击重置按钮，重置登录表单
    resetLoginForm(){
      this.$refs.loginFormRef.resetFields();
    },
    login(){
      this.$refs.loginFormRef.validate(async (valid)=>{
        if(!valid) return;//false 不执行登录
        try {
          const response = await this.$http.get('/login', { 
            params: this.loginForm // 将 param 对象作为查询参数  
          });  
          // 处理响应数据  
          console.log(response.data);
          if(response.data.retCode !== 200) return alert("登录失败！"+response.data.message);
          alert("登录成功！");  
          //保存登录后的token状态
          window.sessionStorage.setItem("token",response.data.data.token);
          this.$router.push("/home");//登录跳转
        } catch (error) {  
          // 处理错误  
          console.error(error);  
        }  
      });
    }
  }
  };
  </script>
   
  <!-- 支持less语法格式，scoped代表样式只在本组件起作用 -->
  <style lang="less" scoped>
    .login_container{
        background-color: #2b4b6b;
        height: 100%;
    }
    .login_box{
        width: 450px;
        height: 300px;
        background-color: #fff;
        border-radius: 3px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        .avatar_box{
          /*less语法的嵌套*/
          height: 130px;
          width: 130px;
          background-color: #fff;
          border: 1px solid #eee;/*加个灰色边框*/
          border-radius: 50%;/*div组件变成圆形*/
          padding: 10px;/*图片和边框之间有10px间隙*/
          box-shadow: 0 0 10px #ddd;/*添加一个阴影，向外扩散10像素*/
          /*头像居中，向左移动box的50%，再向右移动自身的-50%，向上移动该组件高度的-50%，*/
          position: absolute;
          left: 50%;
          transform: translate(-50%,-50%);
          img{
            height: 100%;
            width: 100%;
            border-radius: 50%;
            background-color: #eee;
          }
      }
    }

    .login_form {
        position: absolute;
        bottom: 0px;/*靠下*/
        width: 100%;
        padding: 0 20px;/*上下内边距0，左右内边距20px
        //border-box为元素指定的任何内边距和边框都将在已设定的宽度和高度内进行绘制。
        //实际宽度会是width减去border + padding的计算值
        //此处是为了不让input超出我们的登录父div*/
        box-sizing: border-box;
    }
 
    .btns {
      display: flex;/*flex布局
      //justify-content控制项目在横轴的对齐方式
      //flex-start即左对齐，center 为居中，对应的flex-end为右对齐。*/
      justify-content: flex-end;
    }
  </style>


