<template>
    <div>
        <!-- 面包屑导航 -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card>
            <!-- 搜索与添加区域 -->
            <el-row :gutter="20"><!-- gutter是指定每个分栏的间隔 -->
                <!-- 分栏一共占24格，给搜索框7格，添加按钮4格 -->
                <el-col :span="16">
                    <!-- 搜索与添加区域 -->
                    <el-input placeholder="请输入内容"  v-model="queryInfo.name"  clearable  @clear="getUsersList()">
                        <el-button slot="append" icon="el-icon-search"  @click="getLikeUsersList()"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary"  @click="addDialogVisible = true">添加用户</el-button>
                </el-col>
            </el-row>
            <el-table :data="userList" border stripe><!-- 带边框、斑马纹 -->
                <el-table-column type="index"></el-table-column>
                <el-table-column label="ID" prop="id"></el-table-column>
                <el-table-column label="姓名" prop="name"></el-table-column>
                <el-table-column label="班级" prop="classes"></el-table-column>
                <el-table-column label="年龄" prop="age"></el-table-column>
                <el-table-column label="性别" prop="sex"></el-table-column>
                <el-table-column label="分数" prop="score"></el-table-column>
                <el-table-column label="操作" width="140px">
                    <template slot-scope="scope">
                        <!-- 放置修改、删除和分配角色按钮 -->
                        <el-tooltip effect="dark" content="修改" placement="top">
                            <el-button type="primary" icon="el-icon-edit" size="mini"  @click="showEditDialog(scope.row.id)"></el-button>
                        </el-tooltip>
                        <el-tooltip effect="dark" content="删除" placement="top">
                          <el-button type="danger" icon="el-icon-delete" size="mini"  @click="removeUserById(scope.row.id)"></el-button>
                        </el-tooltip>
                    </template>
                </el-table-column>
            </el-table>
            <el-dialog title="提示" :visible.sync="addDialogVisible" width="50%"  @close="addDialogClosed">
            <!-- 内容主体区 -->
            <el-form :model="addUserForm" :rules="addUserFormRules" ref="addUserFormRef" label-width="70px">
                <el-form-item label="姓名" prop="name"> <!-- prop是验证规则属性 -->
                <el-input v-model="addUserForm.name"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                <el-input v-model="addUserForm.password" type="password"></el-input>
                </el-form-item>
                <el-form-item label="班级" prop="classes">
                <el-input v-model="addUserForm.classes"></el-input>
                </el-form-item>
                <el-form-item label="年龄" prop="age">
                <el-input v-model="addUserForm.age"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                <el-input v-model="addUserForm.sex"></el-input>
                </el-form-item>
                <el-form-item label="分数" prop="score">
                <el-input v-model="addUserForm.score"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部区 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="addDialogVisible = false">取 消</el-button>
                <el-button type="primary"  @click="addUser()">确 定</el-button>
            </span>
            </el-dialog>
            <!-- 修改用户的对话框 -->
        <el-dialog title="修改用户" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed"
            :close-on-click-modal='false'>
              <!-- 内容主体区 -->
              <el-form :model="editUserForm" :rules="editUserFormRules" ref="editUserFormRef" label-width="70px">
                <el-form-item label="用户名" prop="name">
                  <el-input v-model="editUserForm.name" disabled></el-input>
                </el-form-item>
                <el-form-item label="班级" prop="classes">
                  <el-input v-model="editUserForm.classes"></el-input>
                </el-form-item>
                <el-form-item label="年龄" prop="age">
                  <el-input v-model="editUserForm.age"></el-input>
                </el-form-item>
                <el-form-item label="性别" prop="sex">
                  <el-input v-model="editUserForm.sex"></el-input>
                </el-form-item>
                <el-form-item label="分数" prop="score">
                  <el-input v-model="editUserForm.score"></el-input>
                </el-form-item>
              </el-form>
              <!-- 底部区 -->
              <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="editUser()">确 定</el-button>
              </span>
            </el-dialog>
        </el-card>
        
    </div>
</template>
 
<script>
export default {
  data() {
    return {
    //   获取用户列表的参数对象
      queryInfo: {
        name: "",
      },
      userList : [],
      total: 0,
      addDialogVisible: false,
      editDialogVisible: false, //控制修改用户对话框的显示与隐藏
      //添加用户的表单数据
      addUserForm: {},
      editUserForm: {},
      //修改表单的验证规则对象
      editUserFormRules: {
        classes: [{required:false,trigger:'blur'}],
        age: [{required:false,trigger:'blur'}],
        sex: [{required:false,trigger:'blur'}],
        score: [{required:false,trigger:'blur'}]
      },
      //添加表单的验证规则对象
      addUserFormRules: {
        name: [{required:true,message:'请输入姓名',trigger:'blur'},
        {min:3,max:10,message:'姓名长度在3~10个字符',trigger:'blur'}],
        password: [{required:true,message:'请输入密码',trigger:'blur'},
        {min:6,max:15,message:'密码长度在6~15个字符',trigger:'blur'}],
        classes: [{required:true,message:'请输入班级',trigger:'blur'}],
        age: [{required:false,trigger:'blur'}],
        sex: [{required:false,trigger:'blur'}],
        score: [{required:false,trigger:'blur'}]
      }
    };
  },
  created() {
    this.getUsersList();
  },
  methods: {
    async getUsersList() {
      const { data: res } = await this.$http.get("getUsers", {
        params: this.queryInfo,
      });
      if(res.retCode!=200){
          return this.$message.error("获取用户列表失败！");
      }
      console.log(res);
      this.userList = res.data;
      this.total = res.data.length;
    },
    async getLikeUsersList() {
      const { data: res } = await this.$http.get("likeName", {
        params: this.queryInfo,
      });
      if(res.retCode!=200){
          return this.$message.error("获取用户列表失败！");
      }
      console.log(res);
      this.userList = res.data;
      this.total = res.data.length;
    },
    //监听添加用户对话框的关闭状态
    addDialogClosed(){
    this.$refs.addUserFormRef.resetFields();
    },
    // 点击按钮，添加新用户
    addUser(){
      this.$refs.addUserFormRef.validate(async valid =>{
          if(!valid) return;//校验没通过，返回
          //可以发起添加用户的网络请求
          const {data:res} = await this.$http.post('addUser',this.addUserForm);
          if(res.retCode!=200){
            return this.$message.error('添加用户失败！');
          }
          this.$message.success('添加用户成功！');
          // 隐藏添加用户的对话框
          this.addDialogVisible = false;
          //重新获取用户列表数据
          this.getUsersList();
      })
    },
    //关闭编辑用户的对话框
    editDialogClosed(){
      this.$refs.editUserFormRef.resetFields();
    },
    //展示编辑用户的对话框
    async showEditDialog(id){
      const {data:res} = await this.$http.get('findByID/'+id);
      if(res.retCode!=200){
          return this.$message.error('查询用户信息失败！');
      }
      this.editUserForm = res.data;
      this.editDialogVisible = true;
    },
    //点击按钮，修改用户信息
    editUser(){
      this.$refs.editUserFormRef.validate(async valid =>{
          if(!valid) return;//校验没通过，返回
          //可以发起修改用户的网络请求
          const {data:res} = await this.$http.put('UpdateUser/'+
          this.editUserForm.id,{
            classes:this.editUserForm.classes,
            age:this.editUserForm.age,
            sex:this.editUserForm.sex,
            score:this.editUserForm.score,
          });
          if(res.retCode!=200){
            return this.$message.error('修改用户失败！');
          }
          this.$message.success('修改用户成功！');
          // 隐藏修改用户的对话框
          this.editDialogVisible = false;
          //重新获取用户列表数据
          this.getUsersList();
      })
    },
    //根据ID删除对应用户信息
    removeUserById(id){
      //弹框询问用户是否删除数据
      this.$confirm('此操作将永久删除用户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          const {data: res} = await this.$http.delete('DeleteByID/'+id);
          if(res.retCode!==200){
            return this.$message.error('删除用户失败！');
          }
          this.$message({type: 'success',message: '删除用户成功!'});
          //重新获取用户列表数据
          this.getUsersList();
        }).catch(() => {
          this.$message({type: 'info',message: '已取消删除' });          
        });
    }
  },
};
</script>
 
<style scoped>
</style>


