import Vue from 'vue'
import App from './App.vue'
import router from './router'

import ElementUI from 'element-ui'; 
import 'element-ui/lib/theme-chalk/index.css'; 
import './assets/css/global.css'

import axios from 'axios'

import Meta from "vue-meta";

Vue.use(Meta);
axios.defaults.baseURL = 'http://localhost:8081/myapp/users/' //定义根路径(test)
// axios.defaults.baseURL = 'http://localhost:8091/myapp/users/' //定义根路径(dev)
//为axios添加拦截器
axios.interceptors.request.use(config =>{
  console.log(config);
  config.headers.Authorization = window.sessionStorage.getItem('token');
  return config;//最后必须返回config
})
Vue.prototype.$http = axios //在原型链上给$http赋值为axios对象

Vue.use(ElementUI)
// Vue.prototype.$confirm = $confirm(message, title, options)
Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



